.ticket{
    height: 180px;
    margin-top: 20px;
    width: 500px;
    background: $bg-ticket;
    border-radius: $b-radius;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    padding: 0 20px;

    @media only screen and (max-width: $screen-md){
        width: 230px;
        height: auto;
    }
}